<script>
export default {
  name: 'designs',
  props: ['id'],
  data: () => ({
    sections: [],
    currentIndex: 0
  }),
  beforeMount () {
    this.sections = Object.keys(this.$store.state.data.website.designs)
  },
  methods: {
    prePage () {
      if (this.currentIndex === 0) this.currentIndex = this.sections.length - 1
      else this.currentIndex--
    },
    nextPage () {
      if (this.currentIndex === this.sections.length - 1) this.currentIndex = 0
      else this.currentIndex++
    }
  }
}
